    >
      <h1 class="va-h5 mb-4">Ë∞∑Ê≠åËÆ§ËØÅ</h1>
      <!-- <VaForm ref="form" @submit.prevent="submit"> -->
        <VaForm ref="form">  

        <VaImage
          class="w-full md:w-1/2 lg:w-1/3"
