  </template>
  <script lang="js" setup>
  import { ref } from "vue";
  import { useUserStore } from "../../../stores/user-store";
  import { getUser } from "../../../utils/auth";
  import { getGoogle, bindGoogleCode } from "../../../api/user";
  import { buttonStyles } from "../styles";
  import { useToast } from "vuestic-ui";
  
  const store = useUserStore();
  
  const { init } = useToast();
  
  const emits = defineEmits(["cancel"]);
  const form = ref();
  // const Name = ref<string>(store.userName);
  const googleInfo = ref();
  const google_code = ref('');
  
  const submit=()=>{
    if (!google_code.value) {
      const toastMessage = "Ë∞∑Ê≠åËÆ§ËØÅÁ†ÅÈîôËØØ";    
      init({ message: toastMessage, color: "danger" });    
    }
    if (google_code.value) {
      const username=getUser(); 
      bindGoogleCode({user_name:username, google_code:google_code.value}).then(res=>{ 
        if(res==1){
          init({ message: "Ë∞∑Ê≠åËÆ§ËØÅÁªëÂÆöÊàêÂäü", color: "success" }); 
          emits('cancel') 
          window.location.reload();
        }else{
          init({ message: "Ë∞∑Ê≠åËÆ§ËØÅÁªëÂÆöÂ§±Ë¥•", color: "danger" }); 
          emits('cancel') 
        }
      }).catch(err=>{
        init({ message: "Ë∞∑Ê≠åËÆ§ËØÅÁªëÂÆöÂ§±Ë¥•", color: "danger" }); 
        emits('cancel') 
      })  
    }
    
  };


const getGoogleInfo=async()=>{
  const username=getUser(); 
  const res=await getGoogle(username);
  // console.log(res);
  googleInfo.value=res;
  console.log(googleInfo.value);
}
getGoogleInfo();

// Â§çÂà∂ÂäüËÉΩ
const copyText = async (text) => {
 
//  try {
//    await navigator.clipboard.writeText(text)
//    init({ message: "Â§çÂà∂ÊàêÂäü",color: "success"})
//  } catch (err) {
//    init({ message: 'Â§çÂà∂Â§±Ë¥•', color: 'danger' })
//  }


 if (navigator.clipboard) {
    navigator.clipboard.writeText(text).then(() => {
      // console.log('Â§çÂà∂ÊàêÂäü');
      init({ message: "Â§çÂà∂ÊàêÂäü",color: "success"})
    }).catch(err => {
      // console.error('Â§çÂà∂Â§±Ë¥•', err);
      init({ message: 'Â§çÂà∂Â§±Ë¥•', color: 'danger' })
    });
  } else {
    // Â§áÁî®ÊñπÂºèÔºåÊØîÂ¶Ç‰ΩøÁî® document.execCommand()
    const textarea = document.createElement('textarea');
    textarea.value = text;
    document.body.appendChild(textarea);
    textarea.select();
    document.execCommand('copy');
    document.body.removeChild(textarea);
    // console.log('Â§çÂà∂ÊàêÂäü');
    init({ message: "Â§çÂà∂ÊàêÂäü",color: "success"})
  }



}
</script>
  
